.formControl{
    margin-bottom: 5px;
}

.formControl textarea { 
    resize: none;
    width: 150px;
    height: 40px;
}

.formControl input,
.formControl textarea{
    margin-bottom: 5px;
}

.formControl.error input,
.formControl.error textarea{
    border: solid red 2px;
}

.formControl.error span{
    color: red;
    margin: 5px 0 5px 5px;
}