.page{
    margin: 0 0 30px 30px
}

.checkboxDiv{
    display: flex;
    position: relative;
}

.span{
    position: absolute;
    top: 0px;
    left: 20px;
}

.formSummaryError{
    border: 2px solid #8B0000;
    padding: 5px;
    color: #B22222;
    background-color: #F08080;
    width: 160px;
    margin-bottom: 10px;
    transition: 1s;
}

.formSummaryError:hover{
    background-color: #CD5C5C;
    color: #FFA07A;
}